<script>
import moment from "moment";
import localization from "moment/locale/ru";
import ResponseSlider from "./ResponsesSlider";
moment.locale("ru", localization);

export default {
  name: "requestCustomer",
  data() {
    return {};
  },
  computed: {
    request() {
      return this.$store.getters.getRequestById(this.$route.params.requestId);
    }
  },
  mounted() {
    if (this.request === undefined) {
      this.$store.dispatch("fetchRequestById", this.$route.params.requestId);
    }
  },
  methods: {
    moment
  },
  components: {
    ResponseSlider
